import { axios } from '@/utils/http';

const state = {
  marketTeacher: [],
  orgList:[]
};

const mutations = {
  saveTeacher (state, data) {
    state.marketTeacher = [...data.data.teachers];
  },
  saveOrglist (state, data) {
    state.orgList = [...data.data];
  }
};

const actions = {
  async getTeacher (context, params = {}) {
    context.commit('saveTeacher', await axios.get('/market_teacher/getTeacher', {params}));
  },
  async getOrglist (context, params = {}) {
    context.commit('saveOrglist', await axios.get('/org_chart/getTotalOrglist'));
  }
};

export default {
  namespaced: true,
  state,
  mutations,
  actions
};
