import Login from '@/layout/login';
import Entry from '@/layout/entry';
import Nomatch from '@/layout/nomatch';
import Org from '@/layout/market/org';
import Teacher from '@/layout/market/teacher';
// 控制视图展示的导航守卫
function viewBeforeEnter(to, from, next){
  const limitView = JSON.parse(localStorage.getItem('loginInfo')).limitView;
  const viewId = to.meta.viewId;
  if(limitView.includes(viewId)){
    next('/nomatch'); // 受限
  }else{
    next();
  }
}
let groups = {
  marketManger: {
    groupName: '市场管理'
  },
  studyManger: {
    groupName: '教学管理'
  }
};

let routes = [
  {
    path: '/',
    name: 'entry',
    props(){
      let loginInfo = JSON.parse(localStorage.getItem('loginInfo'));
      return {...loginInfo}; 
    },
    beforeEnter(to, from, next){
      // 如果未登录，则重定向至登录页面
      let loginInfo = JSON.parse(localStorage.getItem('loginInfo'));
      if(!loginInfo){
        next('/login');
      }else if(loginInfo.token.split('.').length !== 3){
        next('/login');
      }else if(!Array.isArray(loginInfo.limitView)){
        next('/login');
      }else{
        next();
      }
    },
    component: Entry,
    children: [
      {
        name: 'org',
        path: 'org',
        component: Org,
        beforeEnter: viewBeforeEnter,
        meta: {
          title: '架构组织',
          groupName: 'marketManger',
          viewId: 'org'
        }
      },
      {
        name: 'teacher',
        path: 'teacher',
        beforeEnter: viewBeforeEnter,
        component: Teacher,
        meta: {
          title: '市场老师',
          groupName: 'marketManger',
          viewId: 'marketTeacher'
        }
      },
      {
        name: 'student',
        path: 'student',
        beforeEnter: viewBeforeEnter,
        component: () => import('@/layout/market/student'),
        meta: {
          title: '招生管理',
          groupName: 'marketManger',
          viewId: 'student'
        },
      },
      {
        path: 'test',
        name: 'test',
        componnet: () => import('@/layout/test'),
        meta: {
          title: '测试',
          groupName: 'studyManger',
          viewId: 'studyManger-test'
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '*',
    name: 'nomatch',
    component: Nomatch
  }
];

// 封装一个可以提取出侧边栏数据的方法
function getMenus(){
  const limitView = JSON.parse(localStorage.getItem('loginInfo')).limitView;
  let rootPath = routes[0].path;
  let original = routes[0].children;
  let menus = Object.keys(groups).map(item => {
    let subMenus = original.filter(val => {
      return (val.meta.groupName === item && !limitView.includes(val.meta.viewId));
    }).map(val => {
      return {
        path: rootPath + val.path,
        title: val.meta.title
      };
    });
    if(subMenus.length === 0){
      return null;
    }
    return {
      groupName: groups[item].groupName,
      sign: item,
      subMenus
    };
  });
  menus = menus.filter(item => item !== null);
  return menus;
}
export { routes, getMenus};